.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 19px 16px;
  height: 51px;
  background-color: white;
  margin-bottom: 24px;
  border-bottom: 1px solid #D9FFF5; }
  .header__logo {
    max-width: 100%;
    height: 50px;
    margin: 0 17px 0 17px; }
  .header__wrapper {
    display: flex;
    align-items: center; }
  .header__divider-logo {
    display: none;
    margin: 0 20px; }
  .header__button-repair {
    margin-right: 16px; }
  .header-desktop__wrapper {
    display: none; }

.sidebar {
  margin-bottom: 24px;
  padding-left: 16px;
  padding-right: 16px; }
