.repair-type {
  margin-top: 24px; }
  .repair-type__desc {
    display: none; }
  .repair-type__mobile {
    padding-left: 16px !important;
    padding-top: 16px !important;
    padding-right: 16px !important; }
  .repair-type__wrap {
    display: flex !important;
    justify-content: center; }
  .repair-type__item {
    width: 240px;
    height: 160px;
    padding: 16px;
    background-color: #FFFFFF;
    box-sizing: border-box;
    border-radius: 6px;
    border: 1px solid #eaeaea;
    box-sizing: border-box;
    position: relative; }
  .repair-type__text {
    margin: 0;
    font-size: 16px;
    font-weight: 500; }
  .repair-type__button {
    background-color: #FFFFFF;
    border: 2px solid #FF3E79;
    background: url(./static/arrow.svg) no-repeat 55% 50%;
    position: absolute;
    right: 16px;
    bottom: 16px; }
