.navigation__list {
  display: flex;
  list-style-type: none;
  overflow: scroll;
  white-space: nowrap;
  padding-bottom: 8px; }

.navigation__item {
  font-weight: 500;
  padding: 8px 9px 8px 16px;
  width: auto; }

.navigation__item--active {
  border: 2px solid #b8ffec;
  border-radius: 10px;
  box-shadow: 0px 4px 5px 0px rgba(126, 126, 130, 0.8);
  background-color: #FFFFFF; }

.navigation__item > a {
  text-decoration: none;
  color: #7E7E82; }
