.round-button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 0;
  background-color: #FF3E79;
  padding: 10px;
  cursor: pointer; }
  .round-button img {
    width: 100%;
    height: 100%; }
  .round-button:hover, .round-button:focus {
    box-shadow: 0px 0px 9px 1px #7E7E82;
    transition: 0.2s; }
  .round-button:hover img {
    transition: 0.3s;
    transform: scale(1.1); }

.buttons-connections {
  display: none; }
  .buttons-connections__menu {
    display: flex; }

.main {
  margin-bottom: 55px; }

.divider {
  background-color: #eaeaea;
  height: 32px;
  width: 2px;
  border-radius: 50px;
  margin: 0 20px 0 16px; }

body {
  font-family: 'TTLakes';
  margin: 0;
  background-color: #f8f8f8; }

.overflow {
  overflow: hidden; }

ul, li, a, button {
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: #1B1C21;
  list-style: none;
  font-family: 'TTLakes'; }

.content {
  transition: all 0.5s; }
  .content--blur {
    -moz-filter: blur(2px);
    -o-filter: blur(2px);
    -ms-filter: blur(2px);
    filter: blur(2px);
    pointer-events: none; }

.h1 {
  font-weight: 700;
  font-size: 28px;
  display: flex;
  align-items: flex-start;
  height: 40px;
  margin-bottom: 24px;
  line-height: 40px;
  padding-left: 12px; }
  .h1::before {
    content: url(./static/highlight.svg);
    position: relative;
    left: -11px; }

.h2 {
  background-image: linear-gradient(to right, #f8f8f8 30%, #ffff 55%);
  color: #7E7E82;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  text-transform: uppercase;
  box-sizing: border-box;
  border-top: 1px solid #41F6D7; }

.button-unwrap {
  font-size: 16px;
  font-weight: 500;
  border: none;
  background-color: #f8f8f8;
  height: 24px;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  display: none; }
  .button-unwrap:hover {
    transform: scale(1.05);
    transition: all 0.5s;
    cursor: pointer; }
  .button-unwrap__arrow {
    margin-right: 16px;
    background-position: 50% 50%;
    width: 7.50px;
    padding: 8px 0 8px 8px; }

.window {
  width: 100%;
  height: 100vw;
  position: absolute;
  z-index: 9;
  background: rgba(255, 255, 255, 0.9);
  display: none; }
  .window--active {
    display: block; }

.swiper-pagination {
  position: static !important;
  margin-top: 16px; }

.swiper-pagination-bullet {
  background-color: #B5B6BC !important;
  width: 12px !important;
  height: 12px !important;
  margin-right: 12px; }

.swiper-pagination-bullet-active {
  background-color: #B5B6BC !important; }

.show-more {
  height: auto; }
