.fix {
  background-color: #f8f8f8;
  margin-top: 24px; }
  .fix__desc {
    display: none; }
  .fix__button {
    display: block; }
  .fix__mobile {
    padding-left: 16px !important;
    background-color: #f8f8f8;
    padding-top: 16px !important;
    padding-right: 16px !important; }
  .fix__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
    height: 72px;
    border: 1px solid #eaeaea;
    border-radius: 6px;
    box-sizing: border-box;
    padding: 16px;
    background-color: #FFFFFF; }
  .fix__container {
    width: 280px;
    background-color: #f8f8f8; }
  .fix__wrap {
    display: flex !important;
    justify-content: center;
    background-color: #f8f8f8; }
  .fix__item:hover {
    cursor: pointer;
    border: 1px solid #FF3E79;
    box-shadow: 0px 0px 9px 1px #FF3E79;
    transition: all 0.7s; }
  .fix__h2 {
    margin: 0;
    padding: 16px;
    width: 100%; }
  .fix__button {
    background-color: #FFFFFF;
    border: 2px solid #FF3E79;
    background: url(./static/arrow.svg) no-repeat 55% 50%; }
