.prices {
  margin-top: 24px; }
  .prices__desc {
    display: none; }
  .prices__mobile {
    padding-left: 16px !important;
    padding-top: 16px !important;
    padding-right: 16px !important; }
  .prices__wrap {
    display: flex !important;
    justify-content: center; }
  .prices__item {
    width: 260px;
    height: 200px;
    padding: 16px;
    background-color: #FFFFFF;
    box-sizing: border-box;
    border-radius: 6px;
    border: 1px solid #eaeaea;
    font-size: 14px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 5px; }
  .prices__subtitle {
    color: #7E7E82;
    margin-bottom: 4px !important; }
  .prices__button {
    width: 120px;
    height: 32px;
    color: white;
    border-radius: 20px;
    text-align: center;
    vertical-align: middle;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 13px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .prices__button img {
      width: 7px;
      height: 9.5px; }
  .prices__section:nth-child(3) {
    display: flex;
    justify-content: space-between;
    align-items: end; }
  .prices__offer {
    padding: 16px; }
  .prices__text {
    font-size: 14px; }
  .prices__receive {
    color: #1B1C21;
    font-weight: 500;
    font-size: 16px;
    margin-top: 16px;
    display: block; }
    .prices__receive::after {
      content: url(./static/Line.svg);
      position: relative;
      bottom: -2px;
      right: -7px; }
  .prices p {
    margin: 0; }
