.call {
  box-sizing: border-box;
  padding: 24px 0;
  width: 320px;
  top: 0;
  left: -320px;
  background-color: #FFFFFF;
  z-index: 10;
  height: 100%;
  transition: 1s;
  position: absolute; }
  .call__inputs {
    display: flex;
    flex-direction: column;
    padding: 0 16px; }
  .call__text {
    color: #7E7E82;
    font-size: 14px;
    font-weight: 500; }
    .call__text > a {
      text-decoration: none;
      color: #FF3E79; }
  .call__tel {
    box-sizing: border-box;
    border-radius: 8px;
    border: #eaeaea 1px solid;
    height: 48px;
    padding: 16px;
    font-size: 16px;
    font-weight: 500;
    font-size: 16px;
    color: #B5B6BC; }
  .call__submit {
    width: 136px;
    height: 32px;
    color: white;
    border-radius: 20px;
    text-align: center;
    vertical-align: middle;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 13px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-self: flex-end; }
    .call__submit img {
      width: 7px;
      height: 9.5px; }
  .call__button {
    margin: 16px; }
  .call--active {
    left: 0;
    transition: 1s;
    width: 100%; }
