.menu {
  box-sizing: border-box;
  width: 320px;
  background-color: white;
  position: absolute;
  top: 0;
  left: -320px;
  transition: 1s;
  z-index: 10;
  min-height: 100%; }
  .menu--active {
    left: 0px;
    transition: 1s;
    width: 100%; }

.menu-mobile {
  display: flex;
  justify-content: start;
  flex-direction: column;
  padding-top: 19px;
  padding-bottom: 32px; }
  .menu-mobile__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
    margin-bottom: 20px; }
  .menu-mobile__logo {
    margin-left: 16px; }
  .menu-mobile__list {
    padding: 0 24px;
    font-weight: 500;
    margin-bottom: 32px; }
  .menu-mobile__item {
    margin-bottom: 32px;
    display: block; }
  .menu-mobile__item {
    height: 25px; }
  .menu-mobile__item > a::after {
    display: block;
    position: relative;
    left: 0;
    top: 5px;
    width: 0;
    height: 2px;
    background-color: #FF3E79;
    content: "";
    transition: width 0.3s ease-out; }
  .menu-mobile__item > a:hover::after,
  .menu-mobile__item > a:focus::after {
    width: 80%; }
  .menu-mobile__item > a:last-child {
    margin-bottom: 0; }
  .menu-mobile__item--active {
    height: 25px; }
  .menu-mobile__item--active > a {
    color: #7E7E82;
    position: relative;
    left: -3px; }
  .menu-mobile__item--active > a:hover::after,
  .menu-mobile__item--active > a:focus::after {
    width: 0; }
  .menu-mobile__item--active::before {
    content: url(./static/highlight-mobile.png);
    vertical-align: middle;
    position: relative;
    left: -24px; }
  .menu-mobile__wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  .menu-mobile__connection {
    padding: 0 24px;
    display: flex;
    flex-direction: column; }
  .menu-mobile__connection > .buttons-connections {
    margin-bottom: 16px;
    display: block; }
  .menu-mobile__connection > .buttons-connections > button {
    margin-right: 16px; }
  .menu-mobile__email {
    margin-bottom: 12px; }
  .menu-mobile__tel {
    margin-bottom: 8px;
    font-size: 24px;
    font-weight: bold; }
  .menu-mobile__lang > button {
    border: none;
    background-color: white;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer; }
  .menu-mobile__lang--active {
    color: #7E7E82; }
